<template>
  <div class="container board write">
    <main class="contents">
      <h1>{{ msg }}</h1> 
      <div class="board-write">
        <ul class="list">
          <li>
            <label class="dt">닉네임</label>
            <div class="dd">
              <span class="input"><input type="text"  :value="$store.state.userInfo.nick" spellcheck="false" readonly placeholder="입력하세요"></span>
            </div>
          </li>
          <li>
            <label class="dt">제목</label>
            <div class="dd">
              <span class="input"><input type="text" v-model="title" spellcheck="false" :placeholder="'입력하세요(최대'+titleMax+'글자)'"></span>
            </div>
          </li>
          <li>
            <label class="dt">내용</label>
            <div class="dd">
              <span class="textarea">
                <textarea class="reply" spellcheck="false" v-model="content" data-ui="autoheight" :placeholder="'입력하세요(최대'+contentMax+'글자)'"></textarea>
                <span class="num"><i class="i">{{contentNow}}</i>/<b class="n">{{contentMax}}</b></span>
              </span>
            </div>
          </li>
          <li>
            <label class="dt">사진</label>
            <Files ref="files" :items="this.files" :max="this.max"/>
            
          </li>
        </ul>
      </div>
    </main>
    <nav class="floatbots">
      <div class="inr">
        <div class="btsbox btn-set">
          <button type="button" class="btn" @click="$router.back()"><i class="fa-solid fa-list"></i><em>취소</em></button>
          <button type="button" class="btn" :disabled="isBtnSave ? false : true" @click="write"><i class="fa-solid fa-pen-to-square"></i><em>저장</em></button>  
        </div>
      </div>
    </nav>
  </div>
</template>

<script>
import db  from '../firebaseConfig.js';
import { collection, addDoc } from "firebase/firestore";
import Files from '../components/Files.vue';
import { getStorage, ref,uploadBytes ,getDownloadURL ,deleteObject } from "firebase/storage";
import store from '@/store';
import ui from '../ui.js';


export default {
  name: 'WriteItem',
  props: {
    msg: String
  },
  components: {
    Files
  },
  data() {
      return {
        title:'',
        titleMax: 50,
        content:'',
        contentNow: 0,
        contentMax: new Intl.NumberFormat().format(1000),
        isBtnSave: false,
        files:[],
        max:5,
      }
  },
  watch:{
    title(){
      this.valCheck();
    },
    content(){
      this.contentNow = new Intl.NumberFormat().format(this.content.length);
      this.valCheck();
    }
  },
  created(){
    ui.init();
  },
  mounted(){
    document.querySelector("input#fileInput").value = "";
    document.querySelector(".header .htit").textContent = '글 쓰기';
  },
  methods: {
    commasDel(str){
      console.log(str);
      if (typeof str == 'number') { return; }
			return parseInt(str.replace(/,/g , ''));
		},
    valCheck(){
      if (this.title.length > 0 && this.content.length > 0) {
        this.isBtnSave = true;
      }else{
        this.isBtnSave = false;
      }
      if(this.title.length > this.titleMax ){
        console.log("내용 글자수 맥스");
        this.title = this.title.slice(0, this.titleMax);
        ui.alert("제목의 글자수는 "+this.titleMax+"자 까지 입니다.");
      }
      if(this.content.length > this.commasDel(this.contentMax) ){
        console.log("내용 글자수 맥스");
        this.content = this.content.slice(0, this.commasDel(this.contentMax));
        ui.alert("내용의 글자수는 "+this.contentMax+"자 까지 입니다.");
      }
    },
    dateForm(d){
      return new Intl.DateTimeFormat('ko-KR',{ dateStyle: 'medium', timeStyle: 'medium'}).format( d )
    },
    async write(){
      console.log("쓰기");
      const $title = this.title;
      const $content = this.content;
      // const $fileInput = document.querySelector("input#fileInput");
      const today = new Date();
      
      // console.log(this.Views.title.trim().length);
     
      
      // https://firebase.google.com/docs/firestore/manage-data/add-data?hl=ko&authuser=0
      // const docRef = await setDoc(doc(db, "bbs"), {
      const docRef = await addDoc(collection(db, "bbs"), {
        title: $title,
        content: $content.replace(/\u0020/g,'&nbsp;').replace(/\n/g,'<br>'),
        timestamp: today,
        uid: store.state.userInfo.uid,
        author: store.state.userInfo.nick,
        avatar: store.state.userInfo.avatar,
        date: this.dateForm( today ),
        count: 0,
        coments:[],
        img: this.files || []
      }).then(()=>{
        console.log("쓰기 성공: ");
        this.$router.push('/list');
      }).catch (e =>{
        console.error("Error adding document: ", e);
      });
      docRef
      
    },
    async fileAdd(){
      const $fileInput = document.querySelector("input#fileInput");
      /* 업로드  */
      // let imgUrl = [];
      const storage = getStorage();
      console.log($fileInput.files[0]);
      if ($fileInput.files[0]) {
        const filename = $fileInput.files[0].name;
        const storageRef = ref(storage, "images/"+filename);
        await uploadBytes( storageRef , $fileInput.files[0] ).then((snapshot) => {
          console.log('Uploaded a blob or file!',storageRef.fullPath , snapshot);

        });
        await getDownloadURL(ref(storage, "images/"+filename))
        .then((url) => {
          this.files.push(url);
          console.log(this.files , this.$refs);
          this.$refs.files.itemSet(this.files);
          $fileInput.value = '';
        })
        .catch((error) => {
          console.log(error);
        });
      }
    },
    async fileDel(index){
      console.log(this.files[index]);
      ui.confirm("첨부한 파일을 삭제하시겠습니까?",{
        ycb: ()=>{
          const storage = getStorage();
          console.log(this.files[index]);
          const desertRef = ref(storage, this.files[index]);
          deleteObject(desertRef).then(() => {
            console.log("파일삭제 성공 ");
            this.files.splice(index, 1);
            this.$refs.files.itemSet(this.files);
          }).catch((error) => { console.log(error); });
        }
      });
      // if ( confirm("첨부한 파일을 삭제하시겠습니까?") ) {
      //   const storage = getStorage();
      //   console.log(this.files[index]);
      //   const desertRef = ref(storage, this.files[index]);
        
      //   await deleteObject(desertRef).then(() => {
      //     console.log("파일삭제 성공 ");
      //     this.files.splice(index, 1);
      //     this.$refs.files.itemSet(this.files);
      //   }).catch((error) => { console.log(error); });
      // }else{
      //   return
      // }
    },
  }
}
</script>

